$primary_color: #007cba;
$secondary_color: #5f5f5f;
$background_color: #dcdcde;
$white_color: #ffffff;

.fb-setting-responsive-control-wrapper {
    .fb-setting-responsive-control {
        margin-bottom: 0;
    }
}

.fb-setting-color-panel-btn {
    display: flex !important;
    width: 100%;
    .fb-setting-color-panel-btn-switchr {
        width: 100%;
        justify-content: center;
        height: unset;
        padding: 10px;
        line-height: 16px;
        min-height: 30px;
        font-size: 13px;
        font-weight: 400;
        color: $secondary_color !important;
        box-shadow: inset 0 0 0 1px $background_color;
        &.active {
            background-color: $background_color;
        }
    }
}

/** End General scss for Advanced Button **/

.dnd-items-wrapper {
    list-style: none;
    padding: 0;
    margin: 0;

    li.sortable-item {
        margin-bottom: 10px;
        margin-top: 10px;

        .sortable-header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background: #f9f9f9;
            border: 1px solid #ddd;
            border-radius: 4px;

            .sortable-title {
                flex: 12;
                cursor: pointer;
                overflow: hidden;
                padding-left: 5px;
                text-overflow: ellipsis;
                white-space: nowrap;
                width: 200px;
            }

            .sortable-sort {
                border-right: 1px solid #b4b4cb;
                border-left: 1px solid #b4b4cb;
                button {
                    &:focus {
                        box-shadow: none;
                        outline: none;
                    }
                }
            }

            .sortable-delete {
                button {
                    color: #d63638;
                    &:focus {
                        box-shadow: none;
                        outline: none;
                    }
                }
            }
        }

        .sortable-item-content {
            padding-top: 10px;
        }
    }
}

.fb-social-icon-group-btn {
    display: flex;
}

button {
    &.components-button {
        &.fb-social-icon-single-btn {
            align-items: center;
            display: flex;
            flex: 1;
            justify-content: center;
            padding: 6px;
            height: unset;
            line-height: 16px;
            box-shadow: inset 0 0 0 1px #dcdcde;
            color: #5f5f5f;
            margin-bottom: 10px;
            &.active {
                background-color: #dcdcde !important;
                color: #5f5f5f !important;
            }
        }
        &:focus {
            outline: none !important;
            box-shadow: none !important;
        }
    }
}

// editor.scss

.fb-social-main-wrapper {
    .fb-social-wrapper {
        .fb-social-links-wrapper {
            ul {
                margin: 0;
                padding: 0;
                &.fb-socials {
                    list-style: none;
                    display: flex;
                    flex-wrap: wrap;

                    .fb-social-list-item {
                        a{
                            transition: all 0.3s;
                            &.fb-social-icon-link {
                                box-sizing: border-box;
                                text-decoration: none;
                                cursor: pointer;
                                display: flex;
                                align-items: center;
                                transition: ease-in-out 0.3s;
    
    
                                .fb-social-icon-wrapper {
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    .fb-social-icon {
                                        display: flex;
                                        align-items: center;
                                        justify-content: center;
                                        transition: all 0.3s;
                                        svg {
                                            transition: fill 0.3s;
                                        }
                                    }
    
                                    .fb-social-icon-title {
                                        transition: color 0.3s;
                                    }
                                }
                            }
                            &:focus {
                                outline: none;
                                box-shadow: none;
                            }
                        }
                    }
                }
            }
        }
    }
}